.div-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  background-color: #5C5C5C;
  width: 100%;
  margin: 0;
  position: fixed;
  top: 0;
  z-index: 1;
}

.div-nav-header {
  justify-content: space-around;
}

.a-buttom-active {
  text-decoration: none;
  color: orange;
  border-bottom: 3px solid orange;
}

.a-buttom-active:hover {
  text-decoration: none;
  color: orange;
  border-bottom: 3px solid orange;
}

.a-buttom-responsive-active {
  text-decoration: none;
  color: orange;
  border-bottom: 0px solid orange;
}

.a-buttom-responsive-active:hover {
  text-decoration: none;
  color: orange;
  border-bottom: 0px solid orange;
}

.a-buttom {
  text-decoration: none;
  color: white;
}

.a-buttom:hover {
  text-decoration: none;
  color: orange;

}

.btn-secondary {
  color: #fff;
  background-color: #5C5C5C;
  border-color: #5C5C5C;
}

.btn-secondary:hover {
  background-color: #5C5C5C;
  color: orange;
}

.btn-secondary-active {
  background-color: #5C5C5C;
  color: orange;
  border-bottom: 3px solid orange;
}

.btn-check:focus+.btn-secondary,
.btn-secondary:focus {
  background-color: #5C5C5C;
  color: orange;
  border-bottom: 3px solid orange;
}

.dropdown-menu {
  position: absolute;
  z-index: 1000;
  display: none;
  min-width: 10rem;
  padding: 0.5rem 0;
  margin: 0;
  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #5C5C5C;
  border-radius: 0.25rem;
  background-color: #5C5C5C;
  color: white;
}

.item-d-d {
  display: block;
  width: 100%;
  padding: 0.25rem 1rem;
  clear: both;
  font-weight: 400;
  color: white;
  text-align: inherit;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
}

.item-d-d-selected {
  display: block;
  width: 100%;
  padding: 0.25rem 1rem;
  clear: both;
  font-weight: 400;
  color: orange;
  text-align: inherit;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
}

.dropdown-item:focus,
.dropdown-item:hover {
  color: white;
  background-color: #858585;
}

.btn-check:focus+.btn,
.btn:focus {
  outline: 0;
  box-shadow: none;
}

.dropdown-toggle::after {
  display: inline-block;
  margin-left: 5px;
  vertical-align: 0px;
  content: "";
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}

.a-link-cerrar-sesion {
  color: white;
  cursor: pointer;
  text-decoration: none;
  font-weight: 500;
}

.a-link-cerrar-sesion:hover {
  color: orange;
  cursor: pointer;
  text-decoration: none;
  font-weight: 500;
}

.div-normal {
  display: block;
  width: 100%;
}

.div-responsive {
  display: none;
}

.icon-bars {
  font-size: 30px;
  color: white;
}

.div-header-responsive {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.div-menu-responsive-pre {
  background-color: #fff;
  position: relative;
  visibility: hidden;
  margin-bottom: -30rem;
  opacity: 0;
  border-radius: 0px;
  padding: 20px;
  transition: all 350ms;
}

.div-menu-responsive-post {
  background-color: #fff;
  position: relative;
  visibility: visible;
  margin-bottom: 0rem;
  opacity: 1;
  border-radius: 0px;
  padding: 0px;
  margin-top: -47px;
  transition: all 350ms;
}

.div-menu-responsive {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  background-color: #5C5C5C;
  align-items: center;
}

.div-menu-control-responsive {
  background-color: rgba(0, 25, 71, 0.8);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
}

.div-menu-control-responsive-active {
  position: fixed;
  top: 0;
  width: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: auto;
  width: 100%;
  height: 100%;
  display: block;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: flex-end;
  z-index: 1;
  backdrop-filter: blur(6px);
  background-color: rgba(207, 226, 255, 0.7);
}

.icon-menu-link {
  display: flex;
  cursor: pointer;
}

.p-titulo-nav{
  margin-bottom: 0px;
  color: orange;
  font-size: 20px;
  font-weight: 500;
}

.div-cerrar-sesion-responsive{
  background-color: orange;
  width: 100%;
  text-align: center;
}

.a-link-cerrar-sesion-responsive {
  color: white;
  cursor: pointer;
  text-decoration: none;
  font-weight: 500;
}

.a-link-cerrar-sesion-responsive:hover {
  color: #5C5C5C;
  cursor: pointer;
  text-decoration: none;
  font-weight: 500;
}

@media only screen and (min-device-width: 1400px) {}

@media only screen and (min-device-width: 1200px) and (max-device-width: 1399px) {}

@media only screen and (min-device-width: 992px) and (max-device-width: 1199px) {}

@media only screen and (min-device-width: 768px) and (max-device-width: 991px) {}

@media only screen and (min-device-width: 576px) and (max-device-width: 767px) {

  .div-normal {
    display: none;
  }

  .div-responsive {
    color: #fff;
    display: block;
    width: 100%;
    padding: 0 10px;
  }

}

@media only screen and (max-device-width: 575px) {

  .div-normal {
    display: none;
  }

  .div-responsive {
    color: #fff;
    display: block;
    width: 100%;
    padding: 0 10px;
  }

}